import React, { FC } from 'react';
import { PolygonProps } from './Polygon.types';
import { draw } from '../../svg/svg.methods';
import { ngon, rad } from '../../geometry';

export const Polygon: FC<PolygonProps> = ({
	sides = 6,
	rotation = 0,
	scale = 1
}) => {
	const [points, viewBox] = ngon(sides, scale, rad(rotation), [0, 0]);

	return (<svg xmlns="http://www.w3.org/2000/svg" viewBox='-1 -1 2 2' width="100px" height="100px">
		<path d={draw(ngon(sides, scale, rotation * (Math.PI/180)))} fill="#f00"/>
	</svg>)
}